.page {
  color: #333;
  scroll-behavior: smooth;
  background-color: #e4dfd3;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 24px;
}

.page__body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.page__body--with-menu {
  overflow: hidden;
}

.page__menu {
  transition: transform .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  z-index: 1;
  transform: translateX(0);
}

.page__section--subtitle {
  position: absolute;
}

@media (min-width: 1024px) {
  .page__section--subtitle {
    color: rgba(52, 79, 16, .5);
    height: 18px;
    transform: translateY(118px)rotate(-90deg);
  }
}

